<template>
  <view class="setting-container" :style="{height: `${windowHeight}px`}">
    <view class="menu-list">
      <view
        v-for="(item, index) in industries"
        :key="index"
        class="industry-item"
        :class="{ selected: isSelected(item) }"
        @click="toggleSelection(item)"
      >
        {{ item }}
      </view>
    </view>
    <view class="cu-list menu">
      <view class="cu-item item-box">
        <view class="content text-center" @click="saveSub">
          <text class="text-black">确认订阅</text>
        </view>
      </div>
    </view>
  </view>
  </view>
</template>

<script>
	import storage from '@/utils/storage'
	import constant from '@/utils/constant'
	import {
		saveSubscribe,getSubscribe,getDicts
	} from '@/api/report'
 export default {
   data() {
     return {
       industries: [
       ],
	   point:0,
	   areadys:[],
	   paramForm:{
		   userId: storage.get(constant.userId),
		   selectedIndustries: [],
		   changeAmount:0,
	   },
	
     };
   },
   created() {
	this.getSubDicts()
   	this.getSubscribe(this.paramForm.userId)
	
   },
   methods: {
     saveSub() {
		  const addedIndustriesCount = this.calculateAddedIndustries()* this.point;
		  this.paramForm.changeAmount = addedIndustriesCount
		 this.$modal.confirm(`此次订阅将会扣除您${addedIndustriesCount}积分，是否确认订阅？`).then(() => {
		 saveSubscribe(this.paramForm).then(res => {
		 	this.$modal.showToast('订阅成功')
			this.getSubscribe(this.paramForm.userId)
		 })
		 	})
     },
	 // 计算新增的订阅行业数量
	 calculateAddedIndustries() {
		 
	   const addedIndustries = this.paramForm.selectedIndustries.filter(
	     (industry) => !this.areadys.includes(industry)
	   );
	   return addedIndustries.length;
	 },

	 getSubscribe(userid) {
	 	getSubscribe(userid).then(res => {
	 		    // 更新当前的订阅数据
	 		    this.paramForm.selectedIndustries = res.data.sub;
				this.point = res.data.point
	 		    // 将上一次的订阅数据赋值给 this.areadys
				this.paramForm.selectedIndustries.forEach(item => {
				      this.areadys.push(item);
				    });
		
	 	})
		 ;
	 },
	 getSubDicts() {
	 	getDicts("sys_industry").then(res => {
			 res.data.forEach(item => {
			    this.industries.push(item.dictLabel);
			  });
	 	})
	 },
     toggleSelection(item) {
       const index = this.paramForm.selectedIndustries.indexOf(item);
       if (index === -1) {
         this.paramForm.selectedIndustries.push(item);
       } else {
         this.paramForm.selectedIndustries.splice(index, 1);
       }
     },
     isSelected(item) {
       return this.paramForm.selectedIndustries.includes(item);
     }
   }
 };
</script>

<style lang="scss" scoped>
  .page {
    background-color: #f8f8f8;
  }
  
  .item-box {
    background-color: #FFFFFF;
    margin: 30rpx;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 10rpx;
    border-radius: 8rpx;
    color: #303133;
    font-size: 32rpx;
  }
  
  .menu-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .industry-item {
    width: 48%;
    background-color: #f8f8f8;
    padding: 10rpx;
    margin-bottom: 20rpx;
    border-radius: 8rpx;
    text-align: center;
    box-sizing: border-box;
  }
  
  .industry-item.selected {
    background-color: #007aff;
    color: #ffffff;
  }
</style>
